export const throttle = (fn, ms) => {
	let isThrottled = false
	let savedArgs
	let savedThis

	function wrapper() {

		if (isThrottled) {
			savedArgs = arguments
			savedThis = this
			return
		}

		fn.apply(this, arguments)
		isThrottled = true

		setTimeout(function () {
			isThrottled = false
			if (savedArgs) {
				wrapper.apply(savedThis, savedArgs)
				savedArgs = savedThis = null
			}
		}, ms)
	}

	return wrapper
}